import * as React from 'react';
import { styled } from '@mui/material/styles';
import Box from '@mui/material/Box';
import Paper from '@mui/material/Paper';
import Grid from '@mui/material/Grid';
import { BsEjectFill } from "react-icons/bs";


const Item = styled(Paper)(({ theme }) => ({
  backgroundColor: theme.palette.mode === 'dark' ? '#1A2027' : '#fff',
  ...theme.typography.body2,
  padding: theme.spacing(1),
  textAlign: 'center',
  color: theme.palette.text.secondary,
}));
const target = 2000;

export default function BasicGrid({balance, voucher, getVoucher}) {
    let profits = (balance * 20)/100000000;
  return (
    <div className='boxInfo'>
        <Box sx={{ flexGrow: 1 }}>
        <Grid container spacing={2}>
            <Grid item xs={6} onClick={getVoucher}>
            <Item>
            <div className="headTextLargeBlack"><BsEjectFill /></div>
            {(voucher === true) ? (
                <div className="info">Voucher Obtained</div>
            ) : (<div className="info">Obtain Voucher</div>)}
            </Item>
            </Grid>
            <Grid item xs={6} height={12}>
            {(voucher === true) ? (
            <Item>
                <div className="headTextLargeBlack">{profits}/{target}</div>
                <div className="info">Target</div>
            </Item>
            ) : (
              <Item>
                <div className="headTextLargeBlack">0/{target}</div>
                <div className="info">Target</div>
            </Item>
            )}
            </Grid>
        </Grid>
        </Box>
    </div>
  );
}